body {
  display: flex;
  flex-direction: column;

  max-width: 1366px;
  margin: 0 auto;
  padding: 0;
  border: none;
  box-sizing: border-box;

  font-weight: 400;
  font-style: normal;
  font-stretch: normal;
  letter-spacing: normal;
  font-family: $font-family;
  color: $text-main-color;

  background-color: $background-light-color;
  /* временно для верстки */
  outline: 1px solid #ff0000;
}

img {
  max-width: 100%;
  height: auto;
}

.background {
  background-color: $background-nav-color;
  background-image: url("../img/Rectangle-1-desktop.png");
  background-repeat: no-repeat;
  background-size: 1366px 650px;

  @media (min-resolution: $retina-dpi), (min-resolution: $retina-dppx) {
    background-image: url("../img/Rectangle-1-desktop@2x.png");
    background-size: 1366px 650px;
  }
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}

@media (max-width: $tablet-width) {
  body {
    min-width: 768px;
  }

  .background {
    background-image: url("../img/Rectangle-1-tablet.png");
    background-size: 100% 638px;

    @media (min-resolution: $retina-dpi), (min-resolution: $retina-dppx) {
      background-image: url("../img/Rectangle-1-tablet@2x.png");
      background-size: 100% 638px;
    }
  }
}

@media (max-width: $mobile-width) {
  body {
    min-width: 320px;
  }

  .background {
    background-image: url("../img/Rectangle-1-mobile.png");
    background-size: 100% 500px;

    @media (min-resolution: $retina-dpi), (min-resolution: $retina-dppx) {
      background-image: url("../img/Rectangle-1-mobile@2x.png");
      background-size: 100% 500px;
    }
  }
}
