.home-page {
  width: 100%;
  margin: 0;
  margin-bottom: 120px;
  padding: 0;
  box-sizing: border-box;
}

.home-page__image img {
  width: 560px;
  height: 301px;
}

.home-page__text {
  width: 512px;
  margin-right: 20px;
  padding-top: 56px;
}

.home-page__text h2 {
  margin: 0;
  margin-bottom: 24px;
  padding: 0;

  font-size: 28px;
  font-weight: 600;
  line-height: 42px;
}

.home-page__text p {
  margin: 0;
  padding: 0;

  font-size: 16px;
  line-height: 24px;
}

.home-page__title {
  width: 100%;
  height: 542px;
  margin-bottom: 120px;
  padding-top: 162px;
  padding-left: 198px;
  box-sizing: border-box;

  color: $text-light-color;

  background-position: 0 -108px;
}

.home-page__title h1 {
  margin: 0;
  margin-bottom: 12px;
  padding: 0;

  font-size: 54px;
  font-weight: 700;
  line-height: 66px;
}

.home-page__title p {
  margin: 0;
  font-size: 20px;
  line-height: 24px;
}

.home-page__travel {
  display: flex;
  justify-content: space-between;

  max-width: 1200px;
  margin: 0 auto;
  padding: 0 20px;
  box-sizing: border-box;
}

@media (max-width: $tablet-width) {
  .home-page__image {
    padding-top: 9px;
  }

  .home-page__image img {
    width: 324px;
    height: 264px;
  }

  .home-page__text {
    width: 47.3%;
    margin: 0;
    margin-right: 20px;
    padding: 0;
  }

  .home-page__text h2 {
    width: 205px;
    margin: 0;
    margin-bottom: 16px;

    font-size: 26px;
    line-height: 36px;
  }

  .home-page__text p {
    min-width: 324px;
  }

  .home-page__title {
    height: 558px;
    margin-bottom: 91px;
    padding-top: 188px;
    padding-left: 5.8%;

    background-position: 0 -80px;
  }

  .home-page__title h1 {
    margin: 0;
    margin-bottom: 12px;

    font-size: 50px;
    line-height: 61px;
  }

  .home-page__title p {
    font-size: 18px;
    line-height: 22px;
  }

  .home-page__travel {
    max-width: 100%;
    width: 100%;
    margin: 0;
    padding: 0 45px;
  }
}

@media (max-width: $mobile-width) {
  .home-page__image {
    width: 290px;
    margin: 0 auto;
    padding-top: 0;
  }

  .home-page__image img {
    width: 290px;
    height: 186px;
  }

  .home-page__text {
    width: 100%;
    margin: 0;
    margin-bottom: 33px;

    outline:  1px solid #0000ff;
  }

  .home-page__text h2 {
    margin: 0;
    margin-bottom: 15px;

    font-size: 24px;
    line-height: 34px;
  }

  .home-page__text p {
    min-width: 290px;
  }

  .home-page__title {
    height: 412px;
    margin-bottom: 57px;
    padding-top: 88px;
    padding-left: 15px;

    background-position: 0 -88px;
  }

  .home-page__title h1 {
    margin: 0;
    margin-bottom: 12px;

    font-size: 36px;
    line-height: 45px;
  }

  .home-page__title p {
    width: 200px;
    font-size: 16px;
    line-height: 22px;
  }

  .home-page__travel {
    display: block;

    max-width: 100%;
    width: 100%;
    margin: 0;
    padding: 0 15px;
  }
}
