@charset "UTF-8";
@font-face {
  font-weight: 400;
  font-family: "Montserrat";
  font-style: normal;
  font-display: swap;
  src: url("../fonts/montserrat-regular.woff2") format("woff2"), url("../fonts/montserrat-regular.woff") format("woff"); }

@font-face {
  font-weight: 500;
  font-family: "Montserrat";
  font-style: normal;
  font-display: swap;
  src: url("../fonts/montserrat-medium.woff2") format("woff2"), url("../fonts/montserrat-medium.woff") format("woff"); }

@font-face {
  font-weight: 600;
  font-family: "Montserrat";
  font-style: normal;
  font-display: swap;
  src: url("../fonts/montserrat-semibold.woff2") format("woff2"), url("../fonts/montserrat-semibold.woff") format("woff"); }

@font-face {
  font-weight: 700;
  font-family: "Montserrat";
  font-style: normal;
  font-display: swap;
  src: url("../fonts/montserrat-bold.woff2") format("woff2"), url("../fonts/montserrat-bold.woff") format("woff"); }

body {
  display: flex;
  flex-direction: column;
  max-width: 1366px;
  margin: 0 auto;
  padding: 0;
  border: none;
  box-sizing: border-box;
  font-weight: 400;
  font-style: normal;
  font-stretch: normal;
  letter-spacing: normal;
  font-family: "Montserrat", "Arial", sans-serif;
  color: #011c40;
  background-color: #ffffff;
  /* временно для верстки */
  outline: 1px solid #ff0000; }

img {
  max-width: 100%;
  height: auto; }

.background {
  background-color: #3373b0;
  background-image: url("../img/Rectangle-1-desktop.png");
  background-repeat: no-repeat;
  background-size: 1366px 650px; }
  @media (min-resolution: 144dpi), (min-resolution: 1.5dppx) {
    .background {
      background-image: url("../img/Rectangle-1-desktop@2x.png");
      background-size: 1366px 650px; } }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  clip: rect(0 0 0 0);
  overflow: hidden; }

@media (max-width: 1023px) {
  body {
    min-width: 768px; }
  .background {
    background-image: url("../img/Rectangle-1-tablet.png");
    background-size: 100% 638px; } }
  @media (max-width: 1023px) and (min-resolution: 144dpi), (max-width: 1023px) and (min-resolution: 1.5dppx) {
    .background {
      background-image: url("../img/Rectangle-1-tablet@2x.png");
      background-size: 100% 638px; } }

@media (max-width: 767px) {
  body {
    min-width: 320px; }
  .background {
    background-image: url("../img/Rectangle-1-mobile.png");
    background-size: 100% 500px; } }
  @media (max-width: 767px) and (min-resolution: 144dpi), (max-width: 767px) and (min-resolution: 1.5dppx) {
    .background {
      background-image: url("../img/Rectangle-1-mobile@2x.png");
      background-size: 100% 500px; } }

/* Шапка сайта */
.page-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  min-height: 108px;
  margin: 0;
  padding: 0 7.6%;
  box-sizing: border-box; }

.page-header__logo:hover,
.page-header__logo :focus {
  opacity: 0.5; }

.page-header__nav {
  margin: 0;
  padding: 0; }

.page-header__nav a {
  color: #f9fbfd;
  text-decoration: none; }
  .page-header__nav a:hover {
    color: #8f8f8f; }

.page-header__nav ul {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  margin: 0;
  padding: 0;
  box-sizing: border-box; }

.page-header__nav li {
  margin-top: 10px;
  margin-bottom: 10px;
  margin-left: 33px;
  font-weight: 500;
  font-size: 16px;
  line-height: 20px;
  list-style: none; }
  .page-header__nav li:hover,
  .page-header__nav li :focus {
    color: #8f8f8f; }

.page-header__toggle {
  display: none; }

@media (max-width: 1023px) {
  .page-header {
    min-height: 80px;
    padding: 0 45px; }
  .page-header__logo svg {
    width: 38px;
    height: 20px; }
  .page-header__nav li {
    margin-left: 24px;
    font-size: 14px;
    line-height: 17px; } }

@media (max-width: 767px) {
  .page-header {
    display: block;
    min-height: 0;
    padding: 0 4.7%;
    background-color: #ffffff; }
  .page-header__close {
    background-color: #3373b0; }
  .page-header__path {
    fill: #011c40; }
  .page-header__logo-close {
    fill: #f9fbfd; }
  .page-header__nav a {
    color: #011c40; }
    .page-header__nav a:hover {
      color: #b1b1b1; }
  .page-header__nav ul {
    display: block;
    width: 100%;
    min-height: 237px;
    margin: 0;
    padding: 0;
    box-sizing: border-box; }
  .page-header__nav li {
    margin-top: 21px;
    margin-left: 0;
    padding: 0;
    font-size: 18px;
    line-height: 22px; }
    .page-header__nav li:hover,
    .page-header__nav li :focus {
      color: #b1b1b1; }
  .page-header__toggle {
    display: block; }
    .page-header__toggle:hover,
    .page-header__toggle :focus {
      opacity: 0.5; }
  .page-header__toggle button {
    margin: 0;
    background-repeat: no-repeat;
    background-position: top;
    border: none;
    cursor: pointer; }
  .page-header__toggle-close {
    width: 13px;
    height: 13px;
    background-color: #ffffff;
    background-image: url("../img/icon-close.svg");
    background-size: 13px 13px; }
  .page-header__toggle-open {
    width: 24px;
    height: 12px;
    background-color: #3373b0;
    background-image: url("../img/icon-open.svg");
    background-size: 24px 12px; }
  .page-header__wrapper {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    height: 62px;
    margin-bottom: 26px; }
  .page-header__wrapper--hidden {
    display: none; } }

